<template>
	<view class="container">
		<uni-row style="padding-top: 20px;">
			<view style="text-align: center;">
				<view v-if="!user.commonUserinfoHeadImg" style="width: 200px; height: 200px;"
					class="cu-avatar round bg-white">
					<view class="iconfont icon-people text-gray icon"></view>
				</view>
				<image v-if="user.commonUserinfoHeadImg" @click="handleToAvatar" :src="user.commonUserinfoHeadImg"
					style="width: 200px; height: 200px;" class="cu-avatar round bg-white" mode="widthFix">
				</image>
				<view v-if="user.commonUserinfoUsername" @click="handleToInfo" class="user-info">
					<view class="u_title">
						{{ user.commonUserinfoUsername }}
					</view>
				</view>
			</view>
		</uni-row>
		<tui-calendar ref="calendar" lunar :status="calendarArray" @change="change" ></tui-calendar>
	</view>
</template>

<script>
	import {
		getUserInfo
	} from "@/api/login"

	export default {
		data() {
			return {
				user: {},
				roleGroup: "",
				postGroup: "",
				calendarArray:[{
				    text:"已训练", //描述：2字以内
				    value:"",//状态值 
				    bgColor:"green",//背景色
				    color:"black",  //文字颜色
				    check:true //是否显示对勾 
				},{
				    text:"已训练", //描述：2字以内
				    value:"",//状态值 
				    bgColor:"green",//背景色
				    color:"black",  //文字颜色
				    check:true //是否显示对勾 
				},{
				    text:"已训练", //描述：2字以内
				    value:"",//状态值 
				    bgColor:"green",//背景色
				    color:"black",  //文字颜色
				    check:true //是否显示对勾 
				}]
			}
		},
		onLoad() {
			this.getUserInfo()
		},
		methods: {
			getUserInfo() {
				let userInfo = {
					commonUserinfoId: this.$store.state.user.userId
				}
				getUserInfo(userInfo).then(response => {
					this.user = response.data.records[0]
					this.roleGroup = response.roleGroup
					this.postGroup = response.postGroup
				})
			},
			change(e) {
			  //选择结果
			 console.log(e);
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #ffffff;
	}

	.cu-avatar {
		border: 2px solid #eaeaea;

		.icon {
			font-size: 170px;
		}
	}

	.demo-uni-col {
		height: 36px;
		border-radius: 5px;
	}
</style>
